import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue'
import imgUpload from '../../components/common/img-upload.vue'
import uniPopup from '../../components/uni-popup/uni-popup.vue'

let privacyList = ["仅自己可见", "所有人可见"];

export default {
	components: {
		uniNavBar,
		imgUpload,
		uniPopup
	},
	data() {
		return {
			privacy: "仅自己可见",
			imageList: [],
			textareaLable: "",

			show: true,
			noback: true
		}
	},
	onLoad() {
		this.openPopup();
	},

	onBackPress() {
		console.log("捕捉返回按钮");
		if (this.imageList.length < 1 && this.textareaLable.length < 1) {
			return;
		} else {
			if (this.noback) this.showSaveDialog();
			return this.noback;
		}
	},
	methods: {
		//绑定textrea数据
		bindTextAreaBlur(e) {
			this.textareaLable = e.detail.value;
		},
		//单击返回按钮
		backClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		//单击发布按钮
		publish() {
			console.log("单击发布按钮");
		},
		//单击隐私按钮
		privacyClick() {
			uni.showActionSheet({
				itemList: privacyList,
				success: res => {
					this.privacy = privacyList[res.tapIndex];
				}
			});
		},
		//图片选择监听
		updateImgList(imgList) {
			console.log(imgList);
			this.imageList = imgList;
		},
		//打开糗事编辑内容提示框
		openPopup() {
			this.$refs.popup.open()
		},
		//关闭糗事编辑内容提示框
		closePopup() {
			this.$refs.popup.close()
		},
		//提示框关闭按钮
		iKnowClick() {
			this.closePopup();
		},
		//打开保存提示框
		showSaveDialog() {
			uni.showModal({
				title: "提示",
				content: "是否保存已编辑内容",
				confirmText: "保存",
				cancelText: "不保存",
				success: (e) => {
					console.log("执行保存草稿操作");
					this.noback = false;
					this.backClick();
				},
				fail: () => {
					console.log("取消保存草稿操作");
					this.noback = false;
					this.backClick();
				}
			})
		}

	}
}
